import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import './../../css/nav.css';

class Nav extends Component {
    render() {
        return (
            <nav>
                <NavLink exact to="/" activeClassName="active"><i className={'icon iconfont icon-tubiao-'}></i><span></span></NavLink>
                <NavLink to="/category" activeClassName="active"><i className={'icon iconfont icon-fenlei'}></i><span></span></NavLink>
                <NavLink to="/find" activeClassName="active"><i className={'icon iconfont icon-faxian'}></i><span></span></NavLink>
                <NavLink to="/user" activeClassName="active"><i className={'icon iconfont icon-tubiaolunkuo-'}></i><span></span></NavLink>
            </nav>
        );
    }
}

export default Nav;


