import React, { Component } from 'react';
import $ from 'jquery';
import NProgress from 'nprogress';
import './../../node_modules/nprogress/nprogress.css';
import Nav from "./../view/public/nav";
import './../css/category.css';

class category extends Component {

    componentDidMount() {
        NProgress.start();
        setTimeout(function () {
            NProgress.done();
        }, 1000);
        this.tab();
    }

    scroll() {
        var scrollTop=$(this).scrollTop();
        $('.listwarp .list-item').each(function(){
            var index = $(this).index();
            var H = $(this).height();
            var offsettop=$('.listwarp .list-item').eq(index).offset().top + H-100;
            if(offsettop > scrollTop){
                $('.listnav li').eq(index).addClass('active').siblings().removeClass('active');
                return false;
            }
        });
    }

    tab() {
        document.addEventListener('scroll', this.scroll, true)
        $('.listnav li').click( function () {
            $(this).addClass('active').siblings().removeClass('active');
            var data = $(this).attr("data");
            var step = $('.listwarp .'+data).offset().top;
            alert(step)
            $('.listwarp').scrollTop(step);
            return false;
        })
    }

    render() {
        return (
            <div className={'category'}>
                <div className={'listnav'}>
                    <ul>
                        <li className={'active'} data="category0"><a><span></span></a></li>
                        <li data="category1"><a><span></span></a></li>
                        <li data="category2"><a><span></span></a></li>
                        <li data="category3"><a><span></span></a></li>
                        <li data="category4"><a><span></span></a></li>
                        <li data="category5"><a><span></span></a></li>
                        <li><a><span></span></a></li>
                        <li><a><span></span></a></li>
                        <li><a><span></span></a></li>
                        <li><a><span></span></a></li>
                        <li><a><span></span></a></li>
                        <li><a><span></span></a></li>
                        <li><a><span></span></a></li>
                        <li><a><span></span></a></li>
                        <li><a><span></span></a></li>
                        <li><a><span></span></a></li>
                        <li><a><span></span></a></li>
                        <li><a><span></span></a></li>
                        <li><a><span></span></a></li>
                    </ul>
                </div>
                <div className={'listwarp'}>
                    <div className={'list-item category0'}>
                        <div style={{height:'18rem', background:'red'}}>0</div>
                    </div>
                    <div className={'list-item category1'}>
                        <div style={{height:'18rem', background:'blue'}}>1</div>
                    </div>
                    <div className={'list-item category2'}>
                        <div style={{height:'18rem', background:'red'}}>2</div>
                    </div>
                    <div className={'list-item category3'}>
                        <div style={{height:'18rem', background:'blue'}}>3</div>
                    </div>
                    <div className={'list-item category4'}>
                        <div style={{height:'18rem', background:'red'}}>4</div>
                    </div>
                    <div className={'list-item category5'}>
                        <div style={{height:'18rem', background:'blue'}}>5</div>
                    </div>
                </div>
                <Nav/>
            </div>
        )
    }
}

export default category;


