import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import NProgress from 'nprogress';
import './../../node_modules/nprogress/nprogress.css';
import Nav from "./../view/public/nav"
import './../css/user.css';

class user extends Component {

    componentDidMount() {
        NProgress.start();
        setTimeout(function () {
            NProgress.done();
        }, 1000);
    }

    render() {
        return (
            <div className={'user'}>
                <header>
                    <div className={'img'}></div>
                    <div className={'name'}>/</div>
                </header>
                <div className={'b1'}>
                    <div className={'order'}></div>
                    <div className={'all_order'}><i className={'icon iconfont icon-arrow'}></i></div>
                </div>
                <div className={'b2'}>
                    <Link to=""><i className={'icon iconfont icon-icon-test1'}></i><span></span></Link>
                    <Link to=""><i className={'icon iconfont icon-daishouhuo'}></i><span></span></Link>
                    <Link to=""><i className={'icon iconfont icon-icon-test'}></i><span></span></Link>
                </div>
                <ul className={'items'}>
                    <li>
                        <i className={'icon iconfont icon-fuwuzhongxin'}></i>
                        <div className={'item'}>
                            <div className={'tt'}></div>
                            <div className={'x'}><i className={'icon iconfont icon-arrow'}></i></div>
                        </div>
                    </li>
                    <li>
                        <i className={'icon iconfont icon-wodeyouhui'}></i>
                        <div className={'item'}>
                            <div className={'tt'}></div>
                            <div className={'x'}><i className={'icon iconfont icon-arrow'}></i></div>
                        </div>
                    </li>
                </ul>
                <div className={'ortheritem'}>
                    <div className={'tt'}><i className={'icon iconfont icon-shezhi'}></i><span></span></div>
                    <div className={'x'}><i className={'icon iconfont icon-arrow'}></i></div>
                </div>
                <Nav/>
            </div>
        );
    }
}

export default user;


