import React, { Component } from 'react';

class Login extends Component {
    render() {
        return (
            <div className="Login">
                
            </div>
        );
    }

    constructor(props) {
        super(props);
        this.state = {
            items: ''
        };
    }

    componentDidMount() {
        fetch("http://www.zdomo.com/wap/api/GetBanner", {method: "POST"})
            .then(res => res.json())
            .then(
                (result) => {
                    console.log(result)
                }
            )
    }
}

export default Login;


